import { AnalyticsEvent } from '../types';

export function trackEvent(event: Omit<AnalyticsEvent, 'ts'>): void {
  const payload: AnalyticsEvent = {
    ...event,
    ts: Date.now()
  };

  // Call global hook if defined
  if (typeof window !== 'undefined' && (window as any).onRoomVibeEvent) {
    (window as any).onRoomVibeEvent(payload);
  }

  // Log to console in dev mode
  if (import.meta.env.DEV) {
    console.log('[RoomVibe Event]', payload);
  }
}
