import { RoomPreset, FrameOption } from '../types';

export interface ShareState {
  room: RoomPreset;
  artId: string;
  size: string;
  frame: FrameOption;
  wallColor: string;
  width?: number;
}

export function generateShareLink(state: ShareState): string {
  const params = new URLSearchParams();
  
  params.set('room', state.room);
  params.set('art', state.artId);
  params.set('size', state.size);
  params.set('frame', state.frame);
  params.set('wall', state.wallColor);
  
  if (state.width) {
    params.set('width', state.width.toString());
  }

  const baseUrl = window.location.origin + window.location.pathname;
  return `${baseUrl}?${params.toString()}`;
}

export function parseShareLink(): Partial<ShareState> | null {
  const params = new URLSearchParams(window.location.search);
  
  if (!params.has('room') || !params.has('art')) {
    return null;
  }

  return {
    room: params.get('room') as RoomPreset,
    artId: params.get('art') || '',
    size: params.get('size') || '',
    frame: params.get('frame') as FrameOption,
    wallColor: params.get('wall') || '#FFFFFF',
    width: params.has('width') ? parseInt(params.get('width')!) : undefined
  };
}
