import { Artwork, Theme } from '../types';

export async function submitEmailLead(
  email: string,
  artwork: Artwork,
  theme: Theme,
  _apiKey?: string,
  _groupId?: string
): Promise<boolean> {
  // For MVP, we'll just log this. Real implementation would POST to MailerLite API
  // This requires MAILERLITE_API_KEY and MAILERLITE_GROUP_ID env vars
  
  const payload = {
    email,
    tags: ['roomvibe', `art:${artwork.id}`, `theme:${theme}`],
    fields: {
      artwork_title: artwork.title,
      artwork_id: artwork.id,
      theme: theme
    }
  };

  console.log('[MailerLite] Would submit:', payload);
  
  // In production, you would do:
  // const response = await fetch('https://api.mailerlite.com/api/v2/groups/{_groupId}/subscribers', {
  //   method: 'POST',
  //   headers: {
  //     'Content-Type': 'application/json',
  //     'X-MailerLite-ApiKey': _apiKey
  //   },
  //   body: JSON.stringify(payload)
  // });
  
  return true;
}
