import { Artwork, CheckoutType } from '../types';

export function generateCheckoutLink(
  artwork: Artwork,
  size: string,
  frame: string,
  checkoutType?: CheckoutType,
  customTemplate?: string
): string {
  const template = customTemplate || artwork.checkout.template;
  const type = checkoutType || artwork.checkout.type;

  // Replace template variables
  let link = template
    .replace(/\{\{id\}\}/g, artwork.id)
    .replace(/\{\{size\}\}/g, size)
    .replace(/\{\{frame\}\}/g, frame)
    .replace(/\{\{price\}\}/g, artwork.price.toString())
    .replace(/\{\{variant\}\}/g, `${artwork.id}-${size}-${frame}`);

  // Add UTM parameters
  const url = new URL(link);
  url.searchParams.set('utm_source', 'roomvibe');
  url.searchParams.set('utm_medium', 'widget');
  url.searchParams.set('utm_campaign', type);

  return url.toString();
}
